.nav {
  position: fixed;
  display: flex;
  flex-flow: column wrap;
  padding: 20px;
  width: 100%;
  height: 100%;
  top: 85px;
  right: 0;
  background-color: var(--color2);
  transition: all 0.4s linear;
  display: none;
  z-index: 1000;
}

.nav a {
  display: block;
  text-align: center;
  display: inline-block;
  min-width: 180px;
  padding: 25px 0;
  font-weight: 600;
  margin-bottom: 1px;
  color: var(--color5);
  transition: all 0.3s linear;
}

.nav a:hover {
  color: var(--color4);
  transition: all 0.3s linear;
}

.active {
  color: var(--color4);
}

@media screen and (max-width: 850px) {
  .nav {
    display: flex;
  }
}
